@import url(https://fonts.googleapis.com/css?family=Montserrat:300|Oswald:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300|Oswald:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    overflow-x: hidden;
    background-color: #fef6e4;
}

img {
    margin:0;
}

.text {
 margin-top: 150px;

}

.title{
font-family: 'Oswald', sans-serif;
font-weight: 700;
color: #001858;
}
.description{
    font-family: 'Monserrat', sans-serif;
    font-weight: 300;
    color: #172c66;
}

.portada {
    width: 500px;
    height: auto;
    margin-top: -100px;
}

.btn {
    background-color:#f582ae;
    border: hidden;
    width: 200px;
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
    font-size: x-large;
    color: #001858;
}
.btn:hover{
    background-color: #8bd3dd;
}

.btn-info{
    margin-top: 20px;
    margin-right: 10px;
}

@media screen and (max-width: 760px) {
    .portada {
        margin-top: 100px;
    }
}

@media screen and (max-width: 480px) { 
    
    .text {
        margin-top: 50px;
    }
    .title, .description, .btn {
        margin-left: 30px;
    }
    .col-md-4 {
        text-align: center;
        padding: 0;
    }
    .col {
        padding: 0;
    }
    .portada {
        width: 370px;
        height: auto;
        margin: 10px 0 0 0;
    }  
    .row {
        margin: 0;
    }
    .btn{
        margin-top: 20px;
    }
}



.container {
    margin-top: 2%;
}

.col-md {
    margin-top: 35px
}

.background {
    background-color: #f3d2c1;
    height: 100vh;
    width: 100vw;
    -webkit-clip-path: polygon(0 53%, 100% 22%, 100% 69%, 0 82%);
            clip-path: polygon(0 53%, 100% 22%, 100% 69%, 0 82%);
    position: absolute;
    top: 0;
    right: 0;
}

.Item {
    width: 25px;
    height: 25px;
    margin-left: 3%;
}

@media screen and (max-width: 480px) {
    .Item {
        margin: 10px 20px 0 0;
    }
}
.footer {
    background-color: #f3d2c1;
    margin-top: 130px;
    padding: 10px 0px;
    font-family: 'Montserrat', sans-serif;
}

.footer a {
    color: black;
}

.footer a:visited {
    color: black;
}

@media screen and (max-width: 480px) {
    .footer{
        margin-top: 50px;
    }
}
