@import url('https://fonts.googleapis.com/css?family=Montserrat:300|Oswald:400,700&display=swap');

body {
    overflow-x: hidden;
    background-color: #fef6e4;
}

img {
    margin:0;
}

.text {
 margin-top: 150px;

}

.title{
font-family: 'Oswald', sans-serif;
font-weight: 700;
color: #001858;
}
.description{
    font-family: 'Monserrat', sans-serif;
    font-weight: 300;
    color: #172c66;
}

.portada {
    width: 500px;
    height: auto;
    margin-top: -100px;
}

.btn {
    background-color:#f582ae;
    border: hidden;
    width: 200px;
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
    font-size: x-large;
    color: #001858;
}
.btn:hover{
    background-color: #8bd3dd;
}

.btn-info{
    margin-top: 20px;
    margin-right: 10px;
}

@media screen and (max-width: 760px) {
    .portada {
        margin-top: 100px;
    }
}

@media screen and (max-width: 480px) { 
    
    .text {
        margin-top: 50px;
    }
    .title, .description, .btn {
        margin-left: 30px;
    }
    .col-md-4 {
        text-align: center;
        padding: 0;
    }
    .col {
        padding: 0;
    }
    .portada {
        width: 370px;
        height: auto;
        margin: 10px 0 0 0;
    }  
    .row {
        margin: 0;
    }
    .btn{
        margin-top: 20px;
    }
}

