
.container {
    margin-top: 2%;
}

.col-md {
    margin-top: 35px
}

.background {
    background-color: #f3d2c1;
    height: 100vh;
    width: 100vw;
    clip-path: polygon(0 53%, 100% 22%, 100% 69%, 0 82%);
    position: absolute;
    top: 0;
    right: 0;
}

.Item {
    width: 25px;
    height: 25px;
    margin-left: 3%;
}

@media screen and (max-width: 480px) {
    .Item {
        margin: 10px 20px 0 0;
    }
}